import { useMemo, useRef } from 'react';
import { map, groupBy } from 'lodash';

import { checkRestrictions } from '@mssgme/shared';
import { useWorkspace } from '@mssgme/hooks';

const arrayCompare = (a, b) => {
    if (a.length !== b.length) {
        return false;
    }

    for (const [index, item] of a.entries()) {
        if (item !== b[index]) {
            return false;
        }
    }

    return true;
};
const arrayListCompare = (a, b) => {
    if (a.length !== b.length) {
        return false;
    }

    for (const [index, item] of a.entries()) {
        if (!arrayCompare(item, b[index])) {
            return false;
        }
    }

    return true;
};

const checkBlocks = (context, blocks) => {
    let warning;
    const kinds = [];

    checkRestrictions(context,  'page-blocks-publish', blocks, (error, block) => {
        if (error) {
            warning ||= error;
            kinds.push(block.kind);
        }

        return false;
    });

    if (warning) {
        warning.blocks = map(groupBy(kinds), (item) => ({
            kind: item[0],
            qty: item.length,
        }));
    }

    return warning;
};

export function usePageCheck(page) {
    const workspace = useWorkspace();
    const ref = useRef();
    const next = (Array.isArray(page) ? page : [page])
        .filter(Boolean)
        .map(({ _id, blocks, theme }) => [_id, blocks, theme]);

    if (!(ref.current && arrayListCompare(ref.current[0], next))) {
        ref.current = [next];
    }

    return useMemo(() => {
        const context = { workspace };
        let warning;

        for (const { _id, blocks, theme } of ref.current[0]) {
            if (!(blocks || theme)) {
                continue;
            }

            warning = checkBlocks(context, blocks) || checkRestrictions(context, 'page-themes:edit', theme);

            if (warning) {
                warning.pageId = _id;

                return warning;
            }
        }
    }, ref.current);
}
