import { WEBSITE_DEFAULT_NAME } from '../constants';
import getHomaPageName from './getHomePageName';

export default (page, isHomePage, language = typeof navigator !== 'undefined' && navigator.language || 'en') => {
    const name = page.name;

    if (isHomePage) {
        if (!name) {
            return getHomaPageName(language);
        }

        for (const [lang, title] of Object.entries(WEBSITE_DEFAULT_NAME)) {
            if (name === title) {
                return getHomaPageName(lang);
            }
        }
    }

    return name;
};
